.carousel-item {
    height: 70vh;
}
.img {
    background:linear-gradient(180deg, rgba(255,0,0,0), rgba(0,0,0,0.5)75%);
}
.DetailPanel2 {
    text-align: center;
    color:#343a40;
    min-height: 100%;
    background:linear-gradient(0deg, rgba(0, 68, 103, 0.5),rgba(0, 0, 0, 1)65%);
    padding: 2% 10% 2% 4%;      
}

.popular-header2 {
    color: #343a40;
    background-color: #fff;
    letter-spacing: 2px;
    padding-top: 1%;
    padding-bottom:.1%;
    line-height: 20px;
}

.popular-header2 h2 {
    filter: drop-shadow( px 2px 2px rgba(, 0, 0, .3));
}

.recent{ 
    transition-delay: 1s;

}
.recent :hover{ 
    transform: scale(1.02);
}
.recent img{
    filter: grayscale(100%);
    transition: all .5s; 

}

.recent img:hover{
    filter:none;
}

.carousel-container button{
margin: -6%;
}
.carousel-container {
    width: 100vh;
}

.favoritewatchlater{ 
    transition-delay: 1s;

}
.favoritewatchlater :hover{ 
    transform: scale(1.02);
}
.favoritewatchlater img{
    border-radius: 10%;
    transition: all .5s; 

}

.favoritewatchlater img:hover{
    filter:none;
}
